<template>
  <div>
    <div class="color-282828 order-item-box">
      <div class="font-15">总金额(元)</div>
      <div class="font-24 tx-center" style="padding: 1rem 0 1.375rem 0;">{{mywt.total_balance}}</div>
      <div
        class="font-12 tx-center color-989898"
        style="padding-bottom: 1.23675rem;"
      >冻结余额：{{mywt.freezing_balance}}元</div>
    </div>
    <div class="bottom-line"></div>
    <router-link to="/personal/withdrawal">
    <div
      class="order-item-box dis-flex align-item-center font-12 color-282828 justify-space-between"
      style="padding: 1.1875rem 1.357rem 1.1875rem 1.143rem;"
      
    >
      提现
      <span>></span>
    </div>
    </router-link>
    <div class="bottom-line"></div>
    <router-link to="/personal/bill">
      <div
        class="dis-flex align-item-center font-12 color-282828 justify-space-between"
        style="padding: 1.1875rem 1.357rem 1.1875rem 1.143rem;"
        @click="bill(mywt.id)"
      >
        账单
        <span>></span>
      </div>
    </router-link>
    <div class="bottom-line"></div>
  </div>
</template>

<script>
import { mywallet } from "@/api/api";
export default {
  data() {
    return {
      mywt: {}
    };
  },
  created() {
    let data = {
      move_id: localStorage.getItem("id"),
      type: localStorage.getItem("type")
    };
    mywallet(data).then(res => {
      console.log(res);
      this.mywt = res.data.data;
    });
  },
  methods: {
    bill(id) {
      this.$router.push({ path: "/personal/bill", query: { id: id } });
    }
  }
};
</script>

<style scoped lang="less">
/* 我的钱包Item 容器 */
.order-item-box {
  padding: 1rem 1.357rem 1.071rem 1.143rem;
}
/* 分隔线 */
.bottom-line {
  height: 0.125rem;
  background-color: #f2f2f2;
}
</style>